import React from 'react';

import Section from '../section';
import SummaryItem from '../summary-item';

const SectionContact = ({ contact }) => {
  if (!contact.length) return null;

  return (
    <Section title="contact">
      {contact.map((item) => (
        <SummaryItem
          key={item.name}
          name={item.name}
          description={
            item.name.toLowerCase() === 'email' ? (
                <a href={`mailto:${item.description}`}>{item.description}</a>
                ) : item.name.toLowerCase() === 'github' ? (
                    <a href={item.description} target="_blank" rel="noopener noreferrer">
                        {item.description}
                        </a>
                    ) : (item.description)
          }
        />
      ))}
    </Section>
  );
};

export default SectionContact;
